<template>
  <div class="wrapper">
    <section id="intro">
      <b-container class="bv-example-row bv-example-row-flex-cols">
        <b-row>
            <b-col>
                <img :src="IntroCenterImg">
            </b-col>
        </b-row>
        <b-row align-v="center" style="position:absolute;top:0;bottom:0;left:0;right:0;background:#a7a7c424">
          <b-col id="intro_center">
            <h1>Welcome<div class="box"></div>
          </h1> 
          </b-col>
          
        </b-row>
      </b-container>
    </section>
     <section id="about">
      <b-container class="bv-example-row">
        <b-row>
            <b-col>
             
            </b-col>
        </b-row>
      </b-container>
    </section>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to KING Test Vue.js App',
      IntroCenterImg: '../../static/img/Elf.png',
    }
  },
  mounted(){
    let self = this;
  }
}
</script>

<style scoped lang="scss">

#intro{
  overflow: hidden;
  position: relative;
  height: 100vh;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../../static/img/HomeBG.jpg);
  img{
    top: 20vh;
    left: 750px;
    position: absolute;
    animation-name: slide-in;
    animation-duration: 4s;
    z-index: 2;
  }
  #intro_center{
    z-index: 1;
    padding: 20px 0;
    &::after{
      content: "";
      background-image:url(../../static/img/HomeBG.jpg);
      background-attachment: fixed;
      background-size: cover;
      background-repeat: no-repeat;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      filter: blur(15px);
    }
    h1{
      display: inline-block;
      animation-name: zoom-in;
      animation-duration: 8s;
    }
  }
}
#about{
  height: 800px;
}
@keyframes slide-in{
  from {left: -300px;top: 100vh}
  to {left:750px;top: 20vh}
}
@keyframes zoom-in{
  from {opacity: 0;}
  to {opacity: 1;}
}


.box{
  position:relative;
}
.box:before{
  content:'';
  position:absolute;
  z-index:2;
  top:-42px;
  right: -50px;
  width:30px;
  height:30px;
  background:#e6e58bd9;
  border-radius:2px;
  transform: rotate(45deg);
  -webkit-animation:box .8s infinite ; 
}
@keyframes box{
  0%{
    top:-42px;
    transform: rotate(90deg); /**/
  }
  20%{
    border-radius:2px;
  }
  50%{
    top:-72px; 
    transform: rotate(45deg);
    border-bottom-right-radius:25px;
  }
  80%{
    border-radius:2px;
  }
  100%{
    top:-42px;
    transform: rotate(0deg);
  }
}
.box:after{
  content:'';
  position:absolute;
  z-index:1;
  top:-12px;
  right: -56px;
  width:44px;
  height:3px;
  background:#eaeaea;
  border-radius:100%;
  -webkit-animation:shadow .8s infinite ; 
}
@keyframes shadow{
  0%,100%{
    right:-56px;
    width:40px;
    background:#eaeaea;
  }
  50%{
    top:-10px;
    right:-60px;   /*讓陰影保持在原位*/
    width:50px;
    height:7px;
    background:#eee;
  }
}
</style>



